<template>
  <div class="about">
    <h1>This is about page</h1>
    <div>计数器{{count}} <button @click="plus">+</button> <button @click="minute">-</button></div>
  </div>
</template>
<script setup>
import {ref} from "vue";

const count = ref(1)

const plus = () => {
  count.value ++
}
const minute = () => {
  count.value --
}

</script>
<style scoped>
.about {
  color: #7265e6;
}
</style>
